.row {
  display: grid;
  grid-template-rows: 100%;
  grid-template-columns: 370px 2px 250px 2px 250px;
  height: 70px;
  width: 100%;
}
.s1_form .row:nth-child(2) {
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 10px;
  margin-top: 25px;
}
.s1_form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.s1_form .input-t-container {
  display: flex;
  align-items: center;
  background-color: inherit;
  border: 1px solid white;
  height: 100%;
}
.s1_form .input {
  position: absolute;
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 45px auto 50px;
  align-items: center;
