.dropdown {
  z-index: 3;
  background-color: #fff;
  position: absolute;
  border-radius: 5px;
  margin-top: 5px;
  display: grid;
  justify-content: center;
  padding: 22px 24px;
}
.dropdown-district {
  grid-template: repeat(5, 1fr) / repeat(3, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 5px;
}
.dropdown-type {
  grid-template: repeat(5, 1fr) / repeat(2, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 5px;
}

.dropdown .checkbox-label {
  font-size: 18px;
  font-weight: 400;
  display: flex;
  align-items: center;
}

.dropdown .checkbox-label-d-all {
  grid-column-start: span 3;
  font-weight: 500;
}
.dropdown .checkbox-label-t-all {
  grid-column-start: span 2;
  font-weight: 500;
}

.dropdown input[type="checkbox"] {
  margin-right: 10px;
  width: 20px;
  height: 20px;
}

/* calender */
.calender-container {
  z-index: 3;
  width: auto;
  display: grid;
  grid-template: auto / repeat(2, 1fr);
  grid-gap: 20px;
  /* height: 350px; */
  background-color: #fff;
  position: absolute;
  border-radius: 5px;
  margin-top: 5px;
  padding: 20px 20px 10px;
}
.calender {
  width: 280px;
}
.calender .month-btn {
  position: absolute;
  left: 0;
  width: 40px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.calender .month-btn-right {
  left: auto;
  right: 0;
}
.calender > .caption {
  height: 28px;
  text-align: center;
  font-weight: 700;
}
.calender .week {
  display: grid;
  grid-template: 30px / repeat(7, 40px);
  justify-content: space-between;
  align-items: center;
}
.calender .week-head {
  margin: 10px 0;
}
.calender .week-body {
  grid-template: 40px / repeat(7, 40px);
  height: 100%;
}
.calender .week-label {
  text-align: center;
  color: #979797;
  font-weight: 500;
  height: 100%;
}

.calender .day {
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  cursor: pointer;
}
.calender .day::before {
  content: "";
  width: 30px;
  height: 30px;
  z-index: -1;
  position: absolute;
  border-radius: 50%;
}
.calender .day::after {
  content: "";
  z-index: -2;
  width: 40px;
  height: 30px;
  position: absolute;
  left: 0;
  top: 0;
}

/* start */
.calender .day-start-single {
  color: white;
  /* background: linear-gradient(90deg, transparent 50%, #c0dee0 0); */
}
.calender .day-start-single::before {
  background-color: #008fa3;
}
.calender .day-start {
  color: white;
  /* background: linear-gradient(90deg, transparent 50%, #c0dee0 0); */
}
.calender .day-start::before {
  background-color: #008fa3;
}
.calender .day-start::after {
  background: linear-gradient(90deg, transparent 50%, #c0dee0 0);
  position: absolute;
  left: 0;
  top: 0;
}

/* in */
.calender .day-in {
  color: white;
  background: linear-gradient(90deg, transparent 0%, #c0dee0 0);
}
/* end */
.calender .day-end {
  color: white;
  /* background: linear-gradient(270deg, transparent 50%, #c0dee0 0); */
}
.calender .day-end::before {
  background-color: #008fa3;
}
.calender .day-end::after {
  background: linear-gradient(-90deg, transparent 50%, #c0dee0 0);
}

/* hover1 */
.calender .day-hover:hover {
  color: white;
}
.calender .day-hover:hover::before {
  background-color: #00bcc8;
}
.calender .day-hover:active::before {
  background-color: #008fa3;
}
/* end-hover1 */
.calender .day-end-hover:hover {
  color: white;
}
.calender .day-end-hover:hover::before {
  background-color: #00bcc8;
}
.calender .day-end-hover:hover::after {
  background: linear-gradient(-90deg, transparent 50%, #c0dee0 0);
}

/* today */
.calender .day-today {
  color: #00bcc8;
  font-weight: 700;
}

/* today */
.calender .day-no {
  color: #cacaca;
}

