header {
  height: 81px;
  display: flex;
  align-items: center;
  padding: 0 22px;
  justify-content: space-between;
  background-color: #00bcc8;
}
.logo {
  margin-right: 13px;
}
.logo-wrapper h2 {
  font-weight: 700;
  margin-top: 5px;
  color: white;
}
.logo-wrapper h1 {
  margin-top: -5px;
  color: white;
}
.logo-wrapper {
  display: flex;
}
nav {
  height: 100%;
  display: flex;
}
nav ul {
  display: flex;
  align-items: center;
  height: 100%;
}
nav li {
  display: flex;
  align-items: center;
  height: 100%;
}
nav li:last-child {
  margin-left: 20px;
}
nav li > a {
  display: flex;
  align-items: center;
  color: #eeeeee;
  padding: 14px;
  height: 100%;
  transition: all 0.2s;
}
nav li > a:hover {
  color: #ffffff;
  transition: all 0.2s;
}

.dropdown-search {
  z-index: 3;
  width: 100%;
  height: 200px;
  bottom: -200px;
  left: 0;
  background-color: #fff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
}

.dropdown-search .search-mask {
  width: 100%;
  height: 100vh;
  background-color: red;
  bottom: 0;
  position: fixed;
  opacity: 0;
}

.nav-search {
  width: 50px;
  height: 100%;
  background-color: inherit;
}

.dropdown-search input {
  border: none;
  border-bottom: 1px solid #676767;
  width: 900px;
  height: 60px;
  background-color: inherit;
  font-size: 22px;
  padding-left: 60px;
}

.dropdown-search img {
  z-index: 1;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.dropdown-cross {
  position: absolute;
  right: 10vw;
  top: 40px;
}

footer {
  width: 100%;
  height: 110px;
  background: #00BCC8;
  padding-left: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

footer ul {
  display: flex;
  margin-bottom: 15px;
}

footer ul > li {
  margin-right: 20px;
}
footer a,
footer p {
  font-size: 14px;
}

.fat-footer{
    background-color: #00BCC8;
    border-bottom: #008992 2px solid;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:10px 0px 0px;
}
.icon_wrapper{
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    cursor: pointer;
}
.collapse-icon{
    position: absolute;
    opacity: 0;
}
.collapse-icon-active{
    opacity: 1;
}
.fat-footer .container{
    display: flex;
    overflow: hidden;
}
.fat-footer .container-hidden{
    height: 0;
    -webkit-animation: collapse-reverse 0.5s ease-in-out;
            animation: collapse-reverse 0.5s ease-in-out;
}
.fat-footer .container-active{
    height: 360px;
    -webkit-animation: collapse 0.5s ease-in-out;
            animation: collapse 0.5s ease-in-out;
}
.fat-footer .title{
    font-weight: bold;
    font-size: 26px;
    color: #404040;
    margin-bottom:25px;
}
.fat-footer .list{
    display: grid;
    grid-auto-flow: column;
    grid-template: repeat(7,1fr) / repeat(4,140px);
    grid-row-gap: 16px;
}

@-webkit-keyframes collapse{
    0%{
        height: 0;
    }
    100%{
        height: 360px;
    }
}

@keyframes collapse{
    0%{
        height: 0;
    }
    100%{
        height: 360px;
    }
}

@-webkit-keyframes collapse-reverse{
    0%{
        height: 360px;
    }
    100%{
        height: 0;
    }
}

@keyframes collapse-reverse{
    0%{
        height: 360px;
    }
    100%{
        height: 0;
    }
}
